/********************************
 * Buttons
 ********************************/

.btn {
	padding: var(--margin-s);
	padding-top: 1.6rem;
	/* fix weird font placement */
	width: 100%;
	max-width: 400px;
	min-width: 250px;
	text-align: center;
	font-size: var(--fontsize-l);
	font-family: var(--font-headings);
	letter-spacing: var(--fontspacing-headings);
	border-radius: var(--borderradius-m);
	cursor: pointer;
	transition: var(--animation-hover);
	text-decoration: none;
}

.btn--primary {
	background-color: var(--color-main);
	color: var(--color-text);
	box-shadow: var(--boxshadow-main);
}

.btn--primary:hover {
	box-shadow: -8px 8px var(--color-contrast);
	transform: translate(2px, -2px);
	background-color: var(--color-main-darker);
}

.btn--secondary {
	background-color: var(--color-secondary);
	color: var(--color-white);
	box-shadow: var(--boxshadow-second);
}

.btn--secondary:hover {
	box-shadow: var(--boxshadow-second-hover);
	transform: translate(2px, -2px);
	background-color: var(--color-secondary-darker);
}

.btn--tertiary {
	background-color: var(--color-white);
	color: var(--color-text);
	box-shadow: -3px 3px var(--color-secondary);
	font-family: var(--font-text);
	letter-spacing: 0;
}

.btn--tertiary:hover {
	box-shadow: -5px 5px var(--color-secondary);
	transform: translate(2px, -2px);
}

.btn--primary:active,
.btn--primary:focus,
.btn--secondary:active,
.btn--secondary:focus {
	transform: translate(-6px, 6px);
	box-shadow: none;
}

.btn--tertiary:active {
	transform: translateX(-3px);
	transform: translateY(3px);
	box-shadow: none;
}

.btn--smaller {
	padding: 10px 5px;
	width: 30%;
	border-radius: var(--borderradius-s);
	font-size: var(--fontsize-s);
	min-width: 190px;
}

.btn--history {
	display: block;
    max-width: 100%;
    margin: 0 auto;
    margin-bottom: var(--margin-xs);
}

@media (max-width: 575.98px) {
	.btn {
		font-size: var(--fontsize-l);
		padding: var(--margin-xs);
		padding-top: 1.4rem;
		/* fix weird font placement */
	}
	.btn--tertiary {
		padding: var(--margin-xs);
	}

	.btn--smaller {
		padding: 10px 5px;
		width: 30%;
		border-radius: var(--borderradius-s);
		font-size: var(--fontsize-s);
		min-width: 190px;
	}
}

.game__btn__container {
	position: fixed;
	left: 0;
	bottom: var(--margin-m);
	width: 100vw;
	margin: 0 auto;
	display: flex;
	flex-direction: row;
	justify-content: space-evenly;
	z-index: 10;
}

.game__btn__wrapper {
	width: 100%;
	max-width: 300px;
}

.game__btn {
	justify-content: center;
	height: 100px;
	width: 100px;
	border-radius: 50%;
	color: var(--color-white);
	font-family: var(--font-headings);
	font-size: var(--fontsize-xl);
	transition: var(--animation-hover);
	box-shadow: var(--boxshadow-second-blurred);
}

.game__btn:hover {
	transform: scale(1.1);
}

.reject {
	background-color: var(--color-contrast);
	float: left;
}
.accept {
	background-color: var(--color-secondary);
	float: right;
}
